const InlineChart = ({
  data,
  chartClass = 'dotline-extrathick',
  textStart,
  textEnd,
}) => {
  let chartString = '0'
  data.map(v => {
    let int = Math.ceil(v)
    chartString = chartString.concat(',' + int.toString())
  })

  return (
    <span className={'sparks text-highlight ' + chartClass}>
      {`${textStart || ''} {${chartString}}`}
      <span className="sparks ">{textEnd || ''}</span>
    </span>
  )
}

const AnimatedTextData = ({ interval = 5 }) => {
  const h1 = heroContent.map(v => v.h1)
  const h2 = heroContent.map(v => v.h2)

  return (
    <>
      <h1 className={styles.h1}>
        <Typewriter
          delay={interval}
          initialData={h1[0]}
          data={h1}
          random={false}
        />
      </h1>
      <h2 className={styles.h2}>
        <Typewriter
          delay={interval}
          initialData={h2[0]}
          data={h2}
          speed={3}
          random={false}
        />
      </h2>
    </>
  )
}

